import React from 'react';
import useTranslation from "../../../hooks/useTranslation";

const CustomPostTypeLabel = ({element}) => {

    if(typeof element.isNative === 'undefined'){
        return null;
    }

    return (
        <React.Fragment>
            {element.isNative
                ?
                <span className={`acpt-badge acpt-badge-native`}>
                    <span className="label">
                        {useTranslation("Native")}
                    </span>
                </span>
                :
                <span className={`acpt-badge acpt-badge-${element.isWooCommerce === true ? 'woocommerce' : 'custom' }`}>
                    <span className="label">
                        {element.isWooCommerce === true ? 'WooCommerce' : useTranslation('Custom') }
                    </span>
                </span>
            }
        </React.Fragment>
    );
};

export default CustomPostTypeLabel;